* body {
  margin: 0 auto;
  padding: 0px;
  box-sizing: border-box;
  display: block;
  font-family: "Exo", sans-serif;  
  width:100%;
  height:100%;
}


.app{
  background-image: url("https://images.unsplash.com/photo-1542065029-be403c4fb94b?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1350&q=80");
  background-size: cover;
  background-position: bottom;
  transition:  0.7s ease-out;  
}

.app.vCl{
  background-image: url("https://images.unsplash.com/photo-1495756111155-45cb19b8aeee?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1489&q=80");
 }

.app.sun{
  background-image: url("https://images.unsplash.com/photo-1602899198097-1b6f775f2afc?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1350&q=80");
 }

.app.fewCl{
  background-image: url("https://images.unsplash.com/photo-1595439953692-49a5b45570ef?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1267&q=80")
}

.app.sCl{
  background-image: url("https://images.unsplash.com/photo-1566010503302-2564ae0d47b6?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1350&q=80")
}

.app.rain{
  background-image: url("https://images.unsplash.com/photo-1498847559558-1e4b1a7f7a2f?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1350&q=80")
}

.app.snow{
  background-image: url("https://images.unsplash.com/photo-1491002052546-bf38f186af56?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1383&q=80")
}

main {
  min-height: 100vh;
  padding: 25px;
  max-width:100vh;
  margin: 0 auto;
  width:100%;
  height:100%;
}

Input {
  text-align: center;
  align-self: center;
  min-height: 55px;
  font-weight: bold;
}

.search-box Input {
  margin-top: -15px;
  font-weight: bold;
  font-size: 25px;
  background: rgb(247, 247, 243);
  background: radial-gradient(
    circle,
    rgba(240, 240, 236, 0.914) 19%,
    rgba(219, 219, 218, 0.76) 79%
  );
  box-shadow: 3px 4px rgba(138, 137, 137, 0.7);
  transition: 0.4s ease;
}

.location-box {
  text-align: center;
  background: rgba(255, 255, 255);
  background: radial-gradient(
    circle,
    rgba(248, 248, 248, 0.8) 19%,
    rgba(212, 208, 208, 0.7) 79%
  );
  border-radius: 7px;
  box-shadow: 3px 4px rgba(138, 137, 137, 0.7);
}

small {
  font-size: 30px;
  padding-left: 2px;
  border-left: 2px solid rgba(22, 22, 22, 0.7);
  border-radius: 2px;
  margin-left: 15px;
}

.location {
  font-size: 50px;
  margin-top: 25px;
  padding-top: 10px;
}

.date {
  font-style: italic;
  font-size: 18px;
  padding-bottom: 20px;
  margin-bottom: 20px;
}

.weather-box {
  color: rgb(56, 53, 53);
  text-align: center;
  margin-top: 15px;
  padding: 15px;
  background: rgb(224, 219, 174);
  background: radial-gradient(
    circle,
    rgba(224, 219, 174, 0.9136029411764706) 19%,
    rgba(214, 214, 199, 0.773546918767507) 79%
  );
  border-radius: 7px;
  box-shadow: 3px 4px rgba(138, 137, 137, 0.7);
  width:100%;
  height:100%;
}

.temp {
  margin-top: 20px;
  font-size: 75px;
}

.feel-temp {
  font-size: 20px;
}

img {
  height: 80px;
  width: 80px;
  margin: 20px 0px;
}

.weather {
  font-size: 25px;
  padding-bottom: 25px;
}
